* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

button {
  background-color: transparent;
  padding: 0;
  border: none;
}

h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}
